import React, { useState, useEffect, Fragment, useCallback } from 'react';
import Spinner from './UIElements/Spinner';
import './GlobalCases.css';
import CountUp from './UIElements/CountUp';
import earthIcon from '../assets/img/earth-globe-symbol-with-business-graphic.svg';
import CountryCases from './CountryCases';

export default () => {
  const [countryCode, setConteryCode] = useState('');
  const [confirmed, setConfirmed] = useState('');
  const [deaths, setDeaths] = useState('');
  const [recovered, setRecovered] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    (async function() {
      try {
        const response = await fetch('https://covid19.mathdro.id/api');
        const data = await response.json();
        setConfirmed(data.confirmed.value);
        setRecovered(data.recovered.value);
        setDeaths(data.deaths.value);
        setIsLoading(false);
      } catch {
        setError('حدث خطاء أثناء جلب البيانات');
      }
    })();
  }, []);

  const onSelect = useCallback(async countryCode => {
    setConteryCode(countryCode);
    setIsLoading(true);
    try {
      const response = await fetch(
        `https://covid19.mathdro.id/api/countries/${countryCode}`
      );
      const data = await response.json();
      setConfirmed(data.confirmed.value);
      setRecovered(data.recovered.value);
      setDeaths(data.deaths.value);
      setIsLoading(false);
      setError('');
    } catch {
      setError('لا توجد بيانات');
    }
  }, []);

  let content;
  if (error) {
    content = <h1 className='error'>{error}</h1>;
  } else if (isLoading) {
    content = <Spinner margin='5rem' />;
  } else {
    content = (
      <div className='content'>
        <div className='info'>
          <h2>الحالات المسجلة</h2>
          <div className='number-circle confirmed'>
            <CountUp target={confirmed} speed={200} />
          </div>
        </div>
        <div className='info'>
          <h2>حالات التعافي</h2>
          <div className='number-circle recovered '>
            <CountUp target={recovered} speed={200} />
          </div>
        </div>
        <div className='info'>
          <h2>حالات الوفاة</h2>
          <div className='number-circle deaths'>
            <CountUp target={deaths} speed={200} />
          </div>
        </div>
      </div>
    );
  }

  return (
    <Fragment>
      <CountryCases onSelect={onSelect} />
      <section className='global'>
        <img
          src={
            countryCode
              ? `https://www.countryflags.io/${countryCode}/shiny/64.png`
              : earthIcon
          }
          alt={countryCode ? countryCode : 'earth'}
        />
        {content}
      </section>
      <footer id='main-footer'>
        &copy; <a href='https://github.com/0DKhalid'>Khalid Ayed</a>
        <div style={{ fontSize: '0.6rem' }}>
          Icons made by{' '}
          <a href='https://www.flaticon.com/authors/freepik' title='Freepik'>
            Freepik
          </a>{' '}
          from{' '}
          <a href='https://www.flaticon.com/' title='Flaticon'>
            www.flaticon.com
          </a>
        </div>
      </footer>
    </Fragment>
  );
};
